<template>
    <div class="detail" ref="detailRef">
        <note
                :des="this.$route.query.des"
                :author="this.$route.query.author"
                :avatar="this.$route.query.avatar"
                :time="this.$route.query.time"
                :detail="blog.files ? blog.files.blog.content : ''"
        ></note>
    </div>
</template>

<script>
  import Note from '../components/Note'

  export default {
    name: 'detail',
    components: {
      'note': Note
    },
    data() {
      return {
        blog: {}
      }
    },
    created() {
      this.getBlog();
    },
    methods: {
      getBlog() {
        this.$ajax.get(`https://api.github.com/gists/${this.$route.query.id}`).then((res) => {
          this.blog = res.data
          this.loading = false
        })
      }
    }
  }
</script>


<style lang="scss" scoped>
    .detail {
        position: absolute;
        top: 46px;
        left: 0;
        right: 0;
        bottom: 0;

        overflow: auto;
        -webkit-overflow-scrolling: touch;
    }
</style>